@use '@mview/scss/utilities/functions' as *;
@use '@mview/scss/utilities/mixins' as *;

.media-player-wrapper {
  position: relative;
  height: 100%;
  max-width: 100%;
  min-width: 200px;
  // max-height: 420px;
  overflow: hidden;
  user-select: none;
  -moz-user-select: none;
  -webkit-user-select: none;

  #player {
    width: 100%;
    height: 100%;
    // max-height: 420px;
    object-fit: cover;
    max-width: 100%;
    outline: none;
  }

  .player-inside {
    background-color: rgba(9, 42, 50, 1);
    // background-image: url('/assets/img/audio-bg.jpg');
    background-size: cover;
    background-repeat: no-repeat;
    background-position: center center;

    mat-icon {
      width: 60px;
      height: 60px;
      font-size: 60px;
    }
  }

  .middle-play-btn {
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
    transition: 0.3s;
    z-index: 100;
    @include circles(48px, pick-color('primary', 'blue'));
    display: flex;
    justify-content: center;
    align-items: center;
    flex-shrink: 0;

    svg {
      width: 24px;
      height: 24px;
      font-size: 24px;
    }
  }

  .thumbnail-snapshot-wrapper {
    position: absolute;
    opacity: 0;
    display: block;
    visibility: visible;
    z-index: 2000;
    width: 174px;
    height: 98px;
    bottom: 36px;
    border-radius: 7px;
    border: 4px solid white;
    box-shadow: 0 1px 2px #00000026;
    transform: translateY(10px) scale(0);
    transform-origin: 50% 100%;
    transition: transform 0.2s ease 0.1s, opacity 0.2s ease 0.1s;
    background: linear-gradient(#0000, #0e0e0ebf);

    &.snapshot-visible {
      opacity: 1;
      transform: translate(0) scale(1);
    }

    &::before {
      border-left: 4px solid #0000;
      border-right: 4px solid #0000;
      border-top: 4px solid #fff;
      bottom: -8px;
      content: '';
      height: 0;
      left: 50%;
      position: absolute;
      transform: translateX(-50%);
      width: 0;
      z-index: 2;
    }

    .snapshot-image {
      width: 100%;
      height: 100%;
    }

    .snapshot-time {
      position: absolute;
      bottom: 2px;
      width: 100%;
      font-size: 13px;
      color: white;
      font-weight: 600;
      text-align: center;
    }
  }
}
